.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
  animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.modal-inner-container {
  position: relative;
  width: 100%;
  height: 100%;
  font-family: Times; }

.modal-title {
  font-size: 5vh;
  position: absolute;
  top: 10%;
  left: 10%;
  color: #C93214; }

.modal-text {
  font-size: 2.5vh;
  position: absolute;
  top: 25%;
  left: 10%;
  right: 10%;
  color: #d2cdbc; }

.modal-overlay {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6); }

.modal-close-btn {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  color: #d2cdbc;
  height: 50px;
  -webkit-clip-path: circle(20px at 50% 50%);
  clip-path: circle(20px at 50% 50%); }

.modal-content {
  position: absolute;
  top: 25%;
  left: 25%;
  right: 25%;
  bottom: 25%;
  background: rgba(0, 0, 0, 0.604);
  background-clip: padding-box;
  overflow: auto;
  outline: none;
  border: 20px solid transparent;
  -webkit-border-image: url("assets/poppening_art.png") 20 round;
  -o-border-image: url("assets/poppening_art.png") 20 round;
  border-image: url("assets/poppening_art.png") 20 round; }



